/*--------------------------------------------------------------
# Posts Slider
--------------------------------------------------------------*/

.aarambha-kits-posts-slider-container .flex-row.aarambha-kits-posts-wrapper .post-thumbnail-wrap {
  width: 50%;  
}
.aarambha-kits-posts-slider-container .grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 10px 15px;
  width: 100%;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.aarambha-kits-posts-slider-container .aarambha-kits-post-detail-wrap {
  width: 100%;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap >*.element-order:not(:last-child) {
  padding-bottom: 10px;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title {
  font-weight: 500;
  padding-bottom: 0;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta,
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-excerpt p{
  margin: 0;
}
.aarambha-kits-posts-slider-container .swiper-button-next, 
.aarambha-kits-posts-slider-container .swiper-button-prev {
  display: block;
  height: 50px;
  width: 50px;
  text-align: center;
  line-height: 50px;
}
.aarambha-kits-posts-slider-container .swiper-button-next::after, .aarambha-kits-posts-slider-container .swiper-button-prev::after {
  line-height: inherit;
  font-size: 24px;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-categories a {
  background-color: #289dcc;
  padding: 5px 10px;
  color: #fff;
  font-size: 12px;
  margin-top: 3px;
  text-transform: uppercase;
  display: inline-block;
}
.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta .byline img{
  border-radius: 50%;
  margin-right: 3px;
}
.aarambha-kits-posts-slider-container .posts-swiper-slider .swiper-button-next,
.aarambha-kits-posts-slider-container .posts-swiper-slider .swiper-button-prev {
  background-image: none;
}


.grid-overlay.aarambha-kits-posts-wrapper{
    position: relative;   
}
.grid-overlay.aarambha-kits-posts-wrapper .post-thumbnail-wrap{
    position: relative;
}
.grid-overlay.aarambha-kits-posts-wrapper .post-thumbnail-wrap:after {
  content: "";
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 0;
  height: 65%;
  -webkit-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
  background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0,0,0,0.75)));
  background-image: -o-linear-gradient(top, transparent, rgba(0,0,0,0.75));
  background-image: linear-gradient(to bottom, transparent, rgba(0,0,0,0.75));
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title a,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-excerpt,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap a,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap a *,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta * {
  color: #fff;
}
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title a:hover,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap a:hover,
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap a:hover * {
  color: #289dcc;
}
.aarambha-kits-posts-wrapper .aarambha-kits-entry-meta > div{
    position: relative;
}
.aarambha-kits-posts-wrapper .aarambha-kits-entry-meta > div:last-child{
    padding-right: 0;
}
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-entry-meta > div::before {
  background: #fff
}
.swiper .swiper-button-next, 
.swiper .swiper-button-prev {
  display: none;
}
.swiper:hover .swiper-button-next, 
.swiper:hover .swiper-button-prev {
  display: block;
}
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap a.post-category:hover{
  color: #fff
}
.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title {
  margin: 0;
}
.grid-overlay.aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
  padding-top: 0;
  padding-bottom: 0px;
}

.aarambha-kits-posts-slider-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap:not(.grid-overlay .aarambha-kits-post-detail-wrap) {
  background-color: #F3F3F3;
}